import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { Inspecao } from './../../../model/objetc/inspecao';
import { InspecaoService } from './../../../service/sigobra/inspecao.service';
import { ObraService } from './../../../service/sigobra/obra.service';
import { Obras } from './../../../model/objetc/obras';
import { TipoinspecaoService } from './../../../service/sigobra/tipoinspecao.service';
import { TipoInspecao } from './../../../model/objetc/tipoinspecao';

@Component({
  selector: 'app-inspecao-inserir',
  templateUrl: './inspecao-inserir.page.html',
  styleUrls: ['./inspecao-inserir.page.scss'],
})
export class InspecaoInserirPage implements OnInit {

  inspecao: Inspecao = new Inspecao();
  obras: Array<Obras>;
  tipoinspecoes: Array<TipoInspecao>;

  constructor(private base: Base, private is: InspecaoService, private tis: TipoinspecaoService,
    private os: ObraService, private router: Router) { }

  ngOnInit() {
    if ((this.is.inspecao !== undefined) && (this.is.inspecao !== null)) {
      this.inspecao = this.is.inspecao;
    }
    this.carregarobras();
    this.carregartipoinspecao();
    
  }

  carregarobras() {
    this.os.pesquisar1(' ').subscribe(
      data => {
        this.base.dismiss();
        this.obras = data as Array<Obras>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao cosultar Obra ' + this.base.tratarErro(error));
      }
    );
  }

  carregartipoinspecao() {
    this.tis.pesquisar(' ').subscribe(
      data => {
        this.base.dismiss();
        this.tipoinspecoes = data as Array<TipoInspecao>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao cosultar Tipo Inspecao ' + this.base.tratarErro(error));
      }
    );
  }

  get editando() {
    return Boolean(this.inspecao.id)
  }

  salvar() {
    this.salvando();
  }

  salvando() {
    if ((this.inspecao.ativo === undefined) || (this.inspecao.ativo === null)) {
      this.inspecao.ativo = true;
    }
    this.base.present();
    this.is.salvar(this.inspecao).subscribe(
      data => {
        this.is.inspecao = new Inspecao();
        this.base.dismiss();
        this.router.navigate(['inspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao salvar inspecao :' + this.base.tratarErro(error));
      }
    );
  }

  editar() {
    this.base.present();
    if ((this.inspecao.ativo !== null) && (this.inspecao.ativo !== undefined)) {
      this.inspecao.ativo = true;
    }
    return this.is.edit(this.inspecao).subscribe(
      data => {
        this.base.dismiss();
        this.router.navigate(['inspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Editar  inspecao ' + this.inspecao.id + this.inspecao.ativo
          + this.base.tratarErro(error));
      }
    );
  }

  selecionarobra(event: any) { }
  selecionartipoinspecao(event: any) {
    console.log(event);
  }

}


